export default {
  data () {
    return {
        page:1,
        jokePanel:[]
    }
  },
  components: {
  },

onLoad(){
    this.init();
  },
  methods: {
      init(params){
          if(params=='go'){
              this.page--
              if(this.page<=1){
                  this.page=1;
              }
          }else if(params=='next')(
              this.page++
          )
          var urls='https://v1.alapi.cn/api/joke?page='+this.page;
          uni.request({
			  url:urls,
          	success: (res) => {
          		this.jokePanel = res.data.data.data;
          	}
          })
      }
  }
}
